.selectedDay{
	display: -ms-flexbox;
	display: flex;
	text-transform: uppercase;
}

.selectedDay > div {
	width: 50%;
}

.selectedDay .localForecast {
	display: -ms-flexbox;
	display: flex;
	text-align: center;
}

.selectedDay .localForecast > div {
	width: 50%;
}

.selectedDay .localForecast .detail {
	line-height: 20pt;
}

.selectedDay .localForecast .detail .dayName {
	font-size: 16pt;
}

.selectedDay .localForecast .detail .description {
	font-size: 10pt;
}

.selectedDay .seaForecast .row {
	display: -ms-flexbox;
	display: flex;
	font-size: 10pt;
	line-height: 20pt;
}

.selectedDay .seaForecast .row .label {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	color: #cccccc;
}



